body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.shopping-list {
  max-width: 95%;
  margin: 0 auto;
  text-align: center;
}

.steak_button {
  width: 80%;
  border-radius: 60px;
  border: none;
  padding: 50px;
}
.steak_button:hover {
  background-color: #333;
}
.steak img {
}

.flex {
  display: flex;
  justify-content: space-between;
  width: 80%;
  margin: 50px auto 20px;
}

.a img {
  max-width: 85%;
  margin-top: 8%;
  border-radius: 30px;
}
.a:hover {
  background-color: #333;
  color: white;
}
.a {
  border-radius: 25px;
}

h1 {
  font-size: 30px;
  padding-top: 30px;
}

.price {
  font-size: 60px;
  font-family: sans-serif;
  width: 60%;
  margin: 90px auto;
}

.price p {
  border: solid 2px;
  border-radius: 25px;
}

.Nedan {
  width: 60%;
  margin: 0 auto;
  color: #134758;
}
